export default function QuestionAnswer({ answer, showCorrectAnswer, onClick }) {


    return(
        <a 
            onClick={onClick}
            className="flex items-center p-3 text-base font-bold text-gray-900 bg-gray-50 rounded-lg hover:bg-gray-100 group hover:shadow dark:bg-gray-600 dark:hover:bg-gray-500 dark:text-white">                
            <span className="flex-1 ml-3">{ answer.text }</span>
            { (answer.is_correct && showCorrectAnswer) && <span className="inline-flex items-center justify-center px-2 py-0.5 ml-3 text-xs font-medium text-indigo-50 bg-green-700 rounded dark:bg-green-700 dark:text-indigo-50">Correct</span>}                            
        </a>        
    );
}