import { createSlice } from '@reduxjs/toolkit'

const initialState = {
    totalQuestions:0,
    totalAnswered:0,
    rightAnswers:0,
    wrongAnswers:0,
}

export const testSlice = createSlice({
  name: 'test',
  initialState,
  reducers: {
    setTotalQuestions: (state, action) => {
      state.totalQuestions = action.payload;
    },
    setTotalAnswered: (state, action) => {
        state.totalAnswered = action.payload;
    },    
    incrementTotalAnswered: (state) => {
      state.totalAnswered ++;
    },
    incrementrightAnswers: (state) => {
        state.rightAnswers ++;
    },    
    incrementWrongAnswers: (state) => {
        state.wrongAnswers ++;
    },        
  },
})

// Action creators are generated for each case reducer function
export const { 
    setTotalQuestions, 
    setTotalAnswered, 
    incrementTotalAnswered,
    incrementrightAnswers,
    incrementWrongAnswers
} = testSlice.actions

export default testSlice.reducer